
@font-face {
  font-family: Boska;
  src: url("../fonts/Boska-VariableItalic.woff2");
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: Panchang;
  src: url("../fonts/Panchang-Variable.woff2");
  font-display: swap;
}
@font-face {
  font-family: Switzer;
  src: url("../fonts/Switzer-Variable.woff2");
  font-display: swap;
}

/* resolution */ 

$r-medium: 1024px;
$r-small: 768px;

/* flex-propriety */

$grid-columns: 10;
$grid-columnsp: 10%;

$grid-columns-md: 7;
$grid-columnsp-md: 14.288%;

/* font-size */

$fs1: 0.7rem;
$fs2: 0.85rem;
$fs3: 1rem;
$fs4: 1.15rem;
$fs5: 1.3rem;
$fs6: 1.6rem;
$fs7: 2.2rem;
$fs8: 2.7rem;

/* font-family */

$font-elegant: 'Boska', 'serif';
$font-body: 'Switzer', 'sans-serif';
$font-display: 'Panchang', 'sans-serif';

/* colors */

/* paddings */

$main-padding: 15px;
$smaller-padding: 5px;

/* mixins */

@mixin font($type, $size, $weight: 400, $style: none, $color) {
  font-family: $type;
  font-size: $size;
  font-weight: $weight;
  font-style: $style;
  color: $color;
}

@mixin xs-screen{
  @media only screen and (max-width: $r-small) {
      @content;
  }
}

@mixin md-screen{
  @media only screen and (max-width: $r-medium) {
      @content;
  }
}