@use "sass:math";
@use "sass:list";
@import './reset';
@import './_variables.scss';


* { 
   box-sizing: border-box
}

body {
  color: #111111;
}

/* background-lines */

/* grid */

.container {
  display: grid; 
  grid-template-columns: repeat($grid-columns, $grid-columnsp); 
  grid-template-rows: 10% 73% 17%; 
  gap: 0px 0px; 
  height: 100vh;
  width: 100vw;
  overflow: hidden;

  @include md-screen {
    grid-template-columns: repeat($grid-columns-md, $grid-columnsp-md);
    grid-template-rows: 10% auto minmax(100px, 17%);
  }

  @include xs-screen {
    grid-template-columns: repeat(3, 33.3%);
    grid-template-rows: 10% auto minmax(100px, 17%); 
  }
}

.container > * {
  overflow: hidden;

  @include xs-screen {
    overflow-y: auto;
  }
}

.flag {
  grid-area: 1 / 1 / 2 / 2;
  background-color: #111;
  display: flex;
  align-items: center;
  justify-content: center;

  @include xs-screen {
    display: none;
  }
}

.title1 {
  grid-area: list.slash(1, $grid-columns - 3, 2, $grid-columns - 2);

  @include md-screen { 
    grid-area: list.slash(1, $grid-columns-md - 3, 2, $grid-columns-md - 2);
  }
}

.title2 {
  grid-area: list.slash(3, $grid-columns - 4, 4, $grid-columns - 3);

  @include md-screen { 
    grid-area: list.slash(3, $grid-columns-md - 4, 4, $grid-columns-md - 3);
  }
}

.block-top-1 { 
  grid-area: list.slash(1, $grid-columns - 2, 2, $grid-columns - 1); 

  @include md-screen { 
    grid-area: list.slash(1, $grid-columns-md - 2, 2, $grid-columns-md - 1); 
  }
}
.block-top-2 { 
  grid-area: list.slash(1,  $grid-columns - 1, 2, $grid-columns); 

  @include md-screen { 
    grid-area: list.slash(1,  $grid-columns-md - 1, 2, $grid-columns-md); 
  }
}
.block-top-3 { 
  grid-area: list.slash(1,  $grid-columns, 2, $grid-columns + 1);

  @include md-screen { 
    grid-area: list.slash(1,  $grid-columns-md, 2, $grid-columns-md + 1);
  }
}

.block-btm-1 { 
  grid-area: list.slash(3, $grid-columns - 3, 4, $grid-columns - 2);

  @include md-screen { 
    grid-area: list.slash(3, $grid-columns-md - 3, 4, $grid-columns-md - 2);
  }
}

.block-btm-2 { 
  grid-area: list.slash(3, $grid-columns - 2, 4, $grid-columns - 1); 

  @include md-screen { 
    grid-area: list.slash(3, $grid-columns-md - 2, 4, $grid-columns-md - 1); 
  }
}

.block-btm-3 { 
  grid-area: list.slash(3, $grid-columns - 1, 4, $grid-columns); 

  @include md-screen { 
    grid-area: list.slash(3, $grid-columns-md - 1, 4, $grid-columns-md); 
  }
}

.block-btm-4 { 
  grid-area: list.slash(3, $grid-columns, 4, $grid-columns + 1); 

  @include md-screen { 
    grid-area: list.slash(3, $grid-columns-md, 4, $grid-columns-md + 1); 
  }
}

.block-lang-pt { 
  grid-area: 3 / 1 / 4 / 2; 

  @include xs-screen {
    
  }
}
.block-lang-en { 
  grid-area: 3 / 2 / 4 / 3;
  
  @include xs-screen {
    
  }
 }

.lang-full {
  @include xs-screen {
    display: none !important;
  }
}

.main-content {
  grid-area: list.slash(2, 1, 3, $grid-columns + 1);
  
  @include md-screen { 
    grid-area: list.slash(2, 1, 3, $grid-columns-md + 1); 
  }

  &__wrapper {
    width: 100vw;
    overflow: hidden;
    
    &__inner {
      width: 100vw;
      height: 73vh;
      display: flex;
      position: relative;
      overflow-x: auto;
      scrollbar-width: thin;

      @include xs-screen {
        display: block;
        height: auto;
        overflow-x: hidden;
      }

      &::-webkit-scrollbar {
        width: 5px;
        height: 8px;
        background-color: #eee;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #bbb;
      }
    }
  }

  &__title-section {
    width: calc(50vw);
    flex: none;
    align-self: center;
    padding-left: $main-padding;

    @include md-screen { 
      width: calc(42.864vw);
    }

    @include xs-screen {
      width: 95vw;
      padding-left: 10px;
      padding-bottom: 20px;
    }

    &__title {
      @include font($font-display, $fs8, 900, none, #f1f1f1);
      text-transform: uppercase;
      text-shadow: 0px -0px 0 #000, 1px 1px 0 #000, -2px 2px 0 #000, 1px -1px 0 #000;

      @include md-screen { 
        @include font($font-display, $fs6, 900, none, #f1f1f1);
      }

      @include xs-screen {
        @include font($font-display, $fs7, 900, none, #f1f1f1);
        margin-bottom: $main-padding;
        padding-top: 5px;
      }
    }

    &__subtitle {
      @include font($font-elegant, $fs3, 600, italic, #111111);

      @include xs-screen {
        @include font($font-elegant, $fs3, 600, italic, #111111);
      }
    }
  }

  &__image-section {
    max-width: calc(71vh * 2.00293255132 + 6vw);
    min-width: calc(71vh * 2.00293255132 + 3vw);

    @include xs-screen {
      max-width: none;
      min-width: none;
      width: auto;
    }

    & img {
      width: auto;
      height: 98%;

      @include xs-screen {
        height: auto;
        width: 100vw;
      }
    }

  }

  &__text-section {
    padding-left: $smaller-padding * 2;
    padding-top: $smaller-padding * 2;
    padding-right: $main-padding;
    width: 500px;
    flex: none;
    @include font($font-body, $fs4, 400, none, #111111);
    hyphens: manual;
    line-height: $fs6;

    @include xs-screen {
      width: auto;
    }

    @include md-screen {
      width: auto;
      max-width: 95vw;  
      @include font($font-body, $fs4, 400, none, #111111);
    }
    
  }

  &__image-gallery {
    padding-left: $smaller-padding * 2;
    padding-top: $smaller-padding * 2;
    padding-right: $main-padding;
    @include font($font-body, $fs4, 400, none, #111111);
    hyphens: manual;
    line-height: $fs6;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 5%;
    white-space: pre-line;

    &__image {
      height: 40%;
      width: 50vh;
      position: relative;

      & img {
        object-fit: cover;
        width: 100%;
        max-height: 100%;
        transition: 0.25s;
      }

      &:hover {
        & .main-content__image-gallery__img-hover {
          display: block;
          position: absolute;
          top: 63%;
          left: 50%;
          transform: translate(-50%, -50%);
          @include font($font-body, $fs4, 400, none, #111111);
          cursor: pointer;
        }

        img {
          opacity: 0;
          cursor: pointer;
        }
      }

      @include xs-screen {
        width: auto;
        height: auto;
        max-width: 400px;

        & img {
          width: 100%;
          max-width: 400px;
          height: auto;
        }
      }
    }

    &__img-hover {
      display: none;
    }

    @include md-screen {
      width: auto;
      @include font($font-body, $fs4, 400, none, #111111);
    }
    
    @include xs-screen {
      justify-content: center;
      align-items: center;
      align-content: center;
      white-space: normal;
      width: 100vw;
    }
  }
}

/* blocks */

div[class*="block-b"] {
  display: flex;
  padding-left: $smaller-padding;
  padding-top: $smaller-padding;
}

div[class*="block-btm-"] {
  @include xs-screen {
    display: none;
  }
}

div[class*="block-t"] {
  display: flex;
  align-items: center;
  justify-content: center;

  @include xs-screen {
    display: none;
  }

}

.block-btm {
  &__year {
    @include font($font-elegant, $fs2, 400, italic, #111111);
  }

  &__title {
    @include font($font-body, $fs4, 600, none, #111111);
  }

  &__arrow img {
    width: 20px;
  }
}

.block-top {
  &__title {
    @include font($font-body, $fs3, 400, none, #111111);
  }
}

.block-link {
  text-decoration: none;
  color: inherit;
}

/* section titles */ 

.section-title {
  @include font($font-elegant, $fs2, 600, italic, #111111);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;

  @include xs-screen {
    display: none;
  }
}

/* background-lines */

.column-lines-container {
  z-index: -100;
  width: 100vw;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  display: grid; 
  grid-template-columns: repeat($grid-columns, $grid-columnsp);
  gap: 0px 0px; 

  @include md-screen {
    grid-template-columns: repeat($grid-columns-md, $grid-columnsp-md);

    & > *:nth-child(-n + #{10 - $grid-columns-md}) {
      display: none;
    }
  }

  @include xs-screen {
    display: none;
  }
}

.bg-lines-column:not(:last-child) {
  border-right: 1px solid #eee;
  height: 100%;
}

.row-lines-container {
  z-index: -100;
  width: 100vw;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  display: grid; 
  grid-template-rows: 10% 73% 17%;
  gap: 0px 0px; 

  @include md-screen {
    grid-template-rows: 10% auto minmax(100px, 17%);
  }

  @include xs-screen {
    display: none;
  }
}

.bg-lines-row:not(:last-child) {
  border-bottom: 1px solid #eee;
  height: 100%;
}
.column-lines-container, .row-lines-container {
  z-index: -1000;
}


/* background-image */

.bg-img {
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: -1000;
}

/* lang-btn */

.lang-btn {
  @include font($font-body, $fs4, 600, none, #111111);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  @include xs-screen {
    @include font($font-body, $fs4, 400, none, #f1f1f1);
    display: block;
    text-align: left;
    margin-bottom: 30px;
  }
}

a {
  color: #111111;
}

/* mobile */

.mobile-projects {
  display: none;

  @include xs-screen {
    width: 100vw;
    height: 17vh;
    min-height: 100px;
    position: fixed;
    bottom: 0px;
    left: 0px;
    display: block;
    border-top: 1px solid #ccc;
    overflow-x: scroll;
    white-space: nowrap;
    background-color: white;
  }

  &__block {
    width: 70vw;
    display: inline-block;
    height: 100%;
    border-left: 1px solid #ccc;
  }
}

.mobile-header {
  height: 9vh;
  border-bottom: 1px solid #ccc;
  width: 100vw;
  overflow: hidden;
  position: fixed;
  display: none;

  @include xs-screen {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $main-padding;
  }
}

.mobile-menu {
  display: none;

  @include xs-screen {
    background-color: #111;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    padding: $main-padding;
    padding-top: 25px;
    overflow: auto;
  }

  &__header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 30px;

    & svg {
      cursor: pointer;
    }
  }

  &__section-title {
    @include font($font-elegant, $fs3, 400, italic, #f1f1f1);
    margin-bottom: 15px;
  }

  &__block-link {
    @include font($font-body, $fs5, 400, none, #f1f1f1);
    text-decoration: none;
    margin-bottom: 30px;
    display: block;
  }

  &__content-block {
    margin-bottom: 60px;
  }
}